.fade-in {
    animation: fade-in 1.5s linear normal;
  }
  @keyframes fade-in {
    from { opacity: 0; }
    to { opacity: 1; }
}

.move-up {
  animation: move-up 1.5s ease normal;
}
@keyframes move-up {
    0% {
      transform: translateY(100px);
    }
    100% { transform: translateY(0px); }
}

.bounce-up {
    animation: bounce-up 2s ease normal;
}
@keyframes bounce-up {
	0%, 20%, 50%, 80%, 100% {transform: translateY(0);}
	40% {transform: translateY(-30px);}
	60% {transform: translateY(-15px);}
    from { opacity: 0; }
    to { opacity: 1; }
}

.bounce-right {
    transform: translateX(0);
    transition: all .5s cubic-bezier(0.64, 2.15, 0.55, 0.1);
}
.bounce-left {
    transform: translateX(0);
    transition: all .5s cubic-bezier(0.47, 1.64, 0.41, 0.8);
}
.bounce:hover {
    .bounce-right {
        transform: translateX(20px);
    }

    .bounce-left {
        transform: translateX(-5px);
    }
}

