.button {
    appearance: none;
    text-decoration: none;
    border-radius: 50px;
    display: inline-block;
    font-size: 20px;
    padding: 11px 128px;
    text-align: center;

    &--light-grey {
        background-color: #F4F4F4;
        color: #4A4D4E;
        border: 1px solid #4A4D4E;
    }

    &--light-grey:hover {
        background-color: #4a4d4e;
        color: #ffffff;
    }

    &--gallery {
        background-color: #ebebeb;
        color: #4a4d4e;
        font-size: 18px;
        width: 328px;
        margin: 30px 50px;
        padding: 10px;
      }

      &--gallery:hover {
        background-color: #4a4d4e;
        color: #ffffff;
      }
}