@import '../../settings/fonts';

.title-arrow {
  width: 50vw;
  height: 100vh;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  position: absolute;
  top: 0;
  right: 0;
  padding-right: 50px;
a{
    display: flex;
    justify-content: center;
    align-items: center;
        text-decoration: none;

}
  &__link {
    color: #fff;
    text-decoration: none;
    cursor: pointer;
    font-size: 24px;
    line-height: 30px;
    font-family: $font-family-vodafone-regular-bold;
    padding: 20px 35px;
  }

  &__svg svg {
    height: 45px;
  }
}
