@import "../../settings/fonts";

.welcome-left-screen {
  color: #ffffff;
  position: absolute;
  top: 0;
  display: flex;
  flex-flow: column;
  justify-content: center;
  width: 50vw;
  height: 100vh;

  &__brand {
    font-family: $font-family-vodafone-regular;
    font-weight: 400;
    font-size: 20px;
    line-height: 28px;
    padding: 0 50px 30px;

    span {
      font-size: 28px;
      line-height: 34px;
      font-weight: 700;
    }
  }

  &__title {
    line-height: 72px;
    font-size: 64px;
    padding: 0 0px 30px 50px;
    text-transform: capitalize;

    &--first-line {
      font-weight: 400;
    }
    &--second-line {
      font-weight: 700;
    }
    &--third-line {
      font-weight: 700;
      color: #fecb00;
    }
  }

  &__description {
    font-size: 20px;
    line-height: 28px;
    padding: 0 50px;
  }

  &__blur {
    width: 50vw;
    background: linear-gradient(
      180.42deg,
      rgba(0, 0, 0, 0.4) 0.35%,
      rgba(0, 0, 0, 0.1) 99.64%
    );
    backdrop-filter: blur(3px);
    position: absolute;
    top: 0;
    z-index: -1;
    height: 100vh;
  }
}


