@import '../../settings/fonts';

* {
    margin: 0;
    padding: 0;
  }
.nav-tabs {
    color: #FFFFFF;
    font-family: $font-family-vodafone-regular;
    font-size: 18px;
    font-weight: 700;
    line-height: 24px;
    height: 82px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    .arrows{
        padding-inline-start: 50px;
        z-index: 10;
    }

    .arrows:active {
        svg {
            fill: #FFFFFF;
        }
    }

    &__container {
        position: relative;
        display: flex;
        flex-direction: row;
        justify-content: flex-end;
        align-items: center;
        height: 100%;
            z-index: 10;

    }

    &__item {
        list-style-type: none;

        &::after {
            content: ' ';
            border-right: 1px solid #FFFFFF;
            margin: 15px;
        }
        &:last-child::after {
            content: '';
            border-right: none;
        }
    }

    &__link {
        cursor: pointer;

        &--active {
            color: #FECB00;
        }
    }

}