@import "../../settings/fonts";
.tabs-content {
  background-color: #fff;
  font-size: 18px;
  color: #4a4d4e;
  height: 100vh;
  width: 50vw;
  position: absolute;
  top: 0;
  right: 0;
  overflow: hidden;

  &__title {
    font-size: 18px;
    padding: 10px 0;
    line-height: 24px;
    font-weight: 700;
    margin-block-start: 30px;
  }

  &__image {
    height: 220px;
    width: 260px;

    &.bounce-up {
      animation-delay: 0.1s;
    }
  }

  &__description {
    font-weight: 400;
    line-height: 24px;
    padding: 15px 50px;
    text-align: center;

    &.bounce-up {
      animation-delay: 0.2s;
    }
  }

  & .move-up {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 100%;
  }
}

.button {
  appearance: none;
  text-decoration: none;
  border-radius: 50px;
  display: inline-block;
  font-size: 20px;
  padding: 11px 128px;
  text-align: center;

  &--light-grey {
    background-color: #f4f4f4;
    color: #4a4d4e;
    border: 1px solid #4a4d4e;
  }

  &.bounce-up {
    animation-delay: 0.3s;
  }
}
.extra__link {
  font-family: $font-family-vodafone-regular;
  font-size: 18px;
  font-weight: 400;
  line-height: 24px;
  letter-spacing: 0em;
  text-align: center;
  margin-block-start: 10px;
  color: #4a4d4e;
  cursor: pointer;
  &:hover {
    text-decoration: underline;
  }
}
