@use '../settings/breakpoints' as *;

@mixin isMediaTablet($direction: min-width) {
  @media all and ($direction: $breakpoint-tablet) {
    @content;
  }
}

@mixin isMediaDesktop($direction: min-width) {
  @media all and ($direction: $breakpoint-desktop) {
    @content;
  }
}

@mixin isMediaFullHd($direction: min-width) {
  @media all and ($direction: $breakpoint-fullhd) {
    @content;
  }
}
