// @use '../../settings/fonts' as *;
@import "../../settings/fonts";

.layout-screen {
  position: relative;

  &--background {
    background-image: url("../../../images/BG\ IMG.jpg");
    height: 97.5vh;
    width: 100vw;
    background-color: rgba(
      15,
      159,
      157,
      0.8
    ); /* Used if the image is unavailable */
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    z-index: -1;
    position: relative;
  }
  &--platform-bg {
    background-image: url("../../../images/BG-platform.png");
  }

  .tabs-navigation {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
z-index: 10;
    &--background {
      background-color: #4a4d4e;
      opacity: 0.8;
      position: absolute;
      mix-blend-mode: multiply;
      top: 0;
      left: 0;
      height: 82px;
      width: 100vw;
    }
  }

  .footer {
    height: 58px;
    width: 100vw;
    background-color: #4a4d4e;
    position: absolute;
    bottom: 0;
    display: flex;
    align-items: center;

    &__link {
      color: #f3f3f4;
      font-family: $font-family-vodafone-regular-bold;
      font-size: 14px;
      line-height: 18px;
      margin: 20px 50px;
      text-decoration: none;
    }
  }

  &__item {
    margin: 0;
    display: inline-block;

    &--1\/2 {
      width: 50%;
    }
  }
}
